body{
    margin: 0;
    padding: 0;
}
a{
    text-decoration: none;
}
nav{
    background-color: whitesmoke;
    width: 100vw;
    height: 10vh;
    
}
.inner-nav{
    display: flex;
    margin: 0 5%;
    justify-content: space-between;
}
.logo{
    width: 20%;
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.logo h3{
    margin: 0;
    padding: 0;
}

.get-info{
    width: 30%;
    height: 10vh;
    display: flex;
    align-items: center;
    justify-content: center;
}
.get-info button{
    width: 7vw;
    height: 6vh;
    border: none;
    border-radius: 5px;
    background-color: black;
    color: white;
}
.get-info button:hover{
    background-color:rgba(0,0,0,0.9) ;
}
.get-info button:disabled{
    background-color: rgb(103, 103, 103);
}
@media(max-width:450px){
    .nav-items ul{
        flex-direction: column;
        padding-top: 10vh;
    } 
    .nav-items li{
        width: 100%;
        padding-left: 15vw;

    }
    .get-info button{
        width: 20vw;
        height: 6vh;
    }
    .hamburger{
        width: 7vw;
        height: 5vh;
        display: flex;
    }
    .hamburger .line{
        width: 2.5vw;
        border: 1px solid black;
        margin-bottom: 0.5vh;
    }
 
  
}
@media(max-width:576px) and (min-width:450px){
    .nav-items ul{
        flex-direction: column;
        padding-top: 10vh;
    } 
    .nav-items li{
        width: 100%;
        padding-left: 15vw;

    }
    .get-info button{
        width: 15vw;
        height: 9vh;
    }
    .hamburger{
        width: 5vw;
        height: 5vh;
        display: flex;
    }
    .hamburger .line{
        width: 2vw;
        border: 1px solid black;
        margin-bottom: 0.5vh;
    }
 
  
}
@media(max-width:768px) and (min-width:576px){
    .nav-items li{
        width: 12vw;
    }
    .get-info button{
        width: 12vw;
        height: 9vh;
    }
    .hamburger{
        display:none;
    }
}
@media(max-width:992px) and (min-width:768px){
    .nav-items li{
        width: 12vw;
    }
    .get-info button{
        width: 10vw;
        height: 9vh;
    }
    .hamburger{
        display:none;
    }
}