.inner-cards{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-left: 5%;
    margin-right: 5%;
}
h3{
    text-align: center;
    font-size: 2.5vw;
    margin: 0;
    margin-top: 3vh;
    margin-bottom:2vh ;
}
h3:hover{
    text-shadow: 1px 1px 10px black;
}
.navbtn{
    display: flex;
    justify-content: space-between;
    padding: 5vw;
}
.navbtn button{
    padding: 8px 12px;
    font-weight: bold;
    border:none;
    background-color: black;
    color:white;
}
button:disabled{
    background-color: rgb(103, 103, 103);
}