.u-card{
    background:  black;
    width: 250px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; 
    border: 2px solid white;
    box-shadow: 1px 1px 10px black;
    margin: 5vh 2vw;
    
}
.img{
    width:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
}
.img img{
    width: 90%;
    margin-top: 2vh;
}
.user-details{
    width:90%;
    margin: 0;
}